.img {
    width: 250px;
    opacity: 0;
    animation: fadeIn 2s ease-in-out forwards;
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

.title-empty {
    font-size: 0.9rem;
    text-align: center;
    color: #000000;
    opacity: 0;
    animation: fadeIn 2s ease-in-out forwards;
}